<template>
  <div id="user-details" ref="user-details">
    <b-row class="mb-4userdetails-horizontal-line">
      <b-col lg="12">
        <b-card-body class="p-0 body-space">
          <p class="mb-0 pt-2" v-if="userData && userData.user_name">
            <span class="mr-2">Agent Name:</span><span class="font-weight-bold text-capitalize">{{ userData.user_name }}</span>
          </p>
          <p class="mb-2 pt-0" v-if="eventData && eventData.eventDetails && eventData.eventDetails[0] && eventData.eventDetails[0].title">
            <span class="mr-2 ">Event:</span><span class="font-weight-bold">{{ eventData.eventDetails[0].title }} | {{ epochConverter() }}</span>
          </p>
          <hr class="mt-3 mb-3" />
          <b-form class="height-355">
            <b-row>
              <b-col lg="4" class="col-md-4 col-12 mb-2">
                <label for="validationServer01">Name<span class="text-danger font-weight-bold">*</span> </label>
                <b-form-input v-model="form.name" placeholder="Enter Name" required></b-form-input>
                <p class="text-danger pr-5 mb-0 pt-1" v-if="validate == 1 && !form.name">
                  <small>Name field is required</small>
                </p>
              </b-col>
              <b-col lg="4" class="col-md-4 col-12 mb-2">
                <label for="validationServer02">Email<span class="text-danger font-weight-bold">*</span></label>
                <b-form-input type="email" v-model="form.email" placeholder="Enter Email" required @input="validateEmail"></b-form-input>
                <p class="text-danger pr-5 mb-0 pt-1" v-if="validate == 1 && !form.email">
                  <small>Email field is required</small>
                </p>
                <p class="text-danger pr-5 mb-0 pt-1" v-if="invalidEmail == 1 && form.email.length > 0"><small>Please enter valid email</small></p>
              </b-col>
              <b-col lg="4" class="col-md-4 col-12 mb-2">
                <label for="validationServer01"
                  >Phone<span class="text-danger font-weight-bold" v-if="eventData.eventDetails[0].location == 'Phone call' && eventLocation.location_address == 'invitee_call'">*</span>
                </label>
                <VueTelInput name="telephone" v-model="form.phone" mode="international" />
                <p class="text-danger pr-5 mb-0 pt-1" v-if="eventData.eventDetails[0].location == 'Phone call' && eventLocation.location_address == 'invitee_call' && validate == 1 && !form.phone">
                  <small>Phone field is required</small>
                </p>
              </b-col>
              <b-col lg="12" class="col-md-4 col-12 mb-2">
                <label for="Notes">Notes</label>
                <b-form-textarea type="textarea" v-model="form.notes" placeholder="Enter Notes"></b-form-textarea>
              </b-col>
              <b-col lg="4" class="col-md-4 col-12 mb-2" v-for="item in customFields" :key="item.index">
                <span v-if="item.type == 'Textarea'">
                  <label>{{ item.name }} <span v-if="item.mandatory" class="text-danger font-weight-bold">*</span></label>
                  <b-form-textarea @input="validateCustom(item.name)" v-model="form[item.name.replace(/ /g, '')]" :placeholder="item.placeholder" :required="item.mandatory"> </b-form-textarea>
                  <p class="text-danger pr-5 mb-0 pt-1" v-if="validate_custom && item.mandatory && !form[item.name.replace(/ /g, '')]">
                    <small>{{ item.name }} field is required</small>
                  </p>
                </span>
                <span v-if="item.type == 'Text'">
                  <label>{{ item.name }}<span v-if="item.mandatory" class="text-danger font-weight-bold">*</span></label>
                  <b-form-input v-model="form[item.name.replace(/ /g, '')]" :placeholder="item.placeholder" :required="item.mandatory"></b-form-input>
                  <p class="text-danger pr-5 mb-0 pt-1" v-if="validate == 1 && item.mandatory && !form[item.name.replace(/ /g, '')]">
                    <small>{{ item.name }} field is required</small>
                  </p>
                </span>
                <span v-if="item.type == 'Date'">
                  <label>{{ item.date }}<span v-if="item.mandatory" class="text-danger font-weight-bold">*</span></label>
                  <b-form-input v-model="form[item.date.replace(/ /g, '')]" :placeholder="item.date" :required="item.mandatory"></b-form-input>
                  <p class="text-danger pr-5 mb-0 pt-1" v-if="validate == 1 && item.mandatory && !form[item.date.replace(/ /g, '')]">
                    <small>{{ item.name }} field is required</small>
                  </p>
                </span>
                <span v-if="item.type == 'Number'">
                  <label>{{ item.name }}<span v-if="item.mandatory" class="text-danger font-weight-bold">*</span></label>
                  <b-form-input type="number" v-model="form[item.name.replace(/ /g, '')]" :placeholder="item.placeholder" :required="item.mandatory"> </b-form-input>
                  <p class="text-danger pr-5 mb-0 pt-1" v-if="validate == 1 && item.mandatory && !form[item.name.replace(/ /g, '')]">
                    <small>{{ item.name }} field is required</small>
                  </p>
                </span>
              </b-col>
            </b-row>
            <b-row>
              <b-col> </b-col>
            </b-row>
            <!-- Button -->
          </b-form>
          <div>
            <hr />
            <b-button size="sm" variant="secondary" class="px-4 float-left" @click="$emit('showSlots')">Back</b-button>
            <b-spinner v-if="loading" class="float-right p-2" label="Loading..."></b-spinner>
            <b-button v-else size="sm" variant="primary" class="btn float-right px-4 " @click="onSubmit">
              Submit
            </b-button>
          </div>
        </b-card-body>
      </b-col>
    </b-row>
    <b-modal dialog-class="max-width-400px" ref="slotTimeUp" hide-header hide-footer size="sm">
      <div class="alert alert-warning mb-0 p-4">
        <h4 class="text-center">Sorry, that time slot is no longer available.</h4>
        <p class="my-2 text-center">Please select another available time slot</p>
        <b-button class="btn-sm mt-4 px-4 py-2 text-center d-flex mx-auto" variant="primary" @click="showSlots">View Slots</b-button>
      </div>
    </b-modal>
  </div>
</template>
<script>
  import moment from "moment";
  import Schedulely from "@/js/schedulely/schedulely";
  import { VueTelInput } from "vue-tel-input";
  export default {
    name: "BookSlot",
    components: { VueTelInput },
    props: {
      eventData: Object,
      userData: Object,
    },
    data() {
      return {
        form: {
          email: "",
          name: "",
          phone: null,
        },
        validate: false,
        domainId: "",
        customFields: [],
        loading: false,
        validate_custom: false,
        eventLocation: {},
        invalidEmail: 0,
      };
    },
    mounted() {
      this.eventLocation = JSON.parse(this.eventData.eventDetails[0].location_info);
      // Get Domain Id
      this.domainId = window.domain;
      //document.getElementById("user-details").scrollIntoView();
      this.$refs["user-details"].scrollIntoView();
      this.customFields = JSON.parse(this.eventData.eventDetails[0].custom_fields);
      if (this.customFields != null) {
        this.customFields.map((item) => {
          this.form[item.name.replace(/ /g, "")] = "";
        });
      } else {
        this.customFields = [];
      }
    },
    methods: {
      async onSubmit() {
        let min_notice = new Date(
          moment()
            .tz(this.eventData.deviceTimezone)
            .format("YYYY-MM-DD HH:mm"),
        );
        let event_min_notice = JSON.parse(this.eventData.eventDetails[0].event_min_notice);
        if (event_min_notice && parseInt(event_min_notice.dataofmin_notice[1]) > 0) {
          if (event_min_notice.dataofmin_notice[0] == "days") min_notice.setDate(min_notice.getDate() + parseInt(event_min_notice.dataofmin_notice[1]));
          else if (event_min_notice.dataofmin_notice[0] == "hours") min_notice.setHours(min_notice.getHours() + parseInt(event_min_notice.dataofmin_notice[1]));
          else if (event_min_notice.dataofmin_notice[0] == "minutes") {
            min_notice.setMinutes(min_notice.getMinutes() + parseInt(event_min_notice.dataofmin_notice[1]));
          }
        }
        let max_limit;
        if (this.eventData.eventDetails[0].category == "one to one") max_limit = 1;
        else max_limit = this.eventData.eventDetails[0].slot_limit;
        let selectedSlot = new Date(
          moment(this.eventData.time.start_unix * 1000)
            .tz(this.eventData.deviceTimezone)
            .format("YYYY-MM-DD HH:mm"),
        );
        let is_valid = true;
        if (this.customFields != null) {
          this.customFields.map((item) => {
            if (item.mandatory && this.form[item.name.replace(/ /g, "")].trim() == "") is_valid = false;
          });
        }
        let phone_valid = false;
        if (this.eventData.eventDetails[0].location == "Phone call" && this.eventLocation.location_address == "invitee_call") {
          if (this.form.phone != null) phone_valid = true;
          else phone_valid = false;
        } else phone_valid = true;
        if (min_notice.getTime() < selectedSlot.getTime()) {
          if (this.form.name.trim() && this.form.email.trim() && is_valid && phone_valid && !this.invalidEmail) {
            this.validate = false;
            this.validate_custom = false;
            this.loading = true;
            var events = {
              title: this.eventData.eventDetails[0].title,
              event_type_id: this.eventData.eventDetails[0].id,
              duration: this.eventData.eventDetails[0].duration,
              description: this.eventData.eventDetails[0].description,
              start_date: new Date(this.eventData.time.start_unix * 1000).toISOString(),
              end_date: new Date(this.eventData.time.end_unix * 1000).toISOString(),
              start_time: moment(this.eventData.time.start_unix * 1000).format("YYYY-MM-DD HH:mm:ss"),
              end_time: moment(this.eventData.time.end_unix * 1000).format("YYYY-MM-DD HH:mm:ss"),
              status: "scheduled",
              location: this.eventData.eventDetails[0].location,
              location_info: this.eventData.eventDetails[0].location_info,
            };
            var events_participants = {
              name: this.form.name,
              email: this.form.email,
              custom_fields: "",
            };
            Object.keys(this.form).length > 0 ? (events_participants.custom_fields = JSON.stringify(this.form)) : (events_participants.custom_fields = "");
            //events_participants.customfields_info = events_participants.custom_fields;
            var data = { events, events_participants };

            try {
              let response = await Schedulely.add(`${this.domainId}/${this.userData.domain_user_id}/events?max_limit=${max_limit}`, data);
              if (response) {
                this.loading = false;
                this.$parent.$parent.$parent.$emit("bookSlotConfirmed", true, this.userData.user_name);
              } else {
                this.loading = false;
              }
            } catch (e) {
              this.loading = false;
              this.$refs.slotTimeUp.show();
            }
          } else {
            this.validate = true;
            this.validate_custom = true;
          }
        } else {
          this.$refs.slotTimeUp.show();
        }
      },
      showSlots() {
        this.$refs.slotTimeUp.hide();
        this.$emit("showSlots");
      },
      validateCustom(name) {
        if (this.form[name.replace(/ /g, "")].trim() == "") this.validate_custom = true;
        else this.validate_custom = false;
      },
      epochConverter() {
        return moment(this.eventData.time.start_unix * 1000)
          .tz(this.eventData.deviceTimezone)
          .format("lll");
      },

      // Email validation
      validateEmail(email) {
        var regex = /^([A-Za-z0-9_+-.]{0,80})@[^-][A-Za-z0-9-]+(\.[A-Za-z0-9-]+)*(\.[A-Za-z]{2,})$/;
        this.invalidEmail = 0;
        if (!regex.test(email)) this.invalidEmail = 1;
      },
    },
  };
</script>

